import React from 'react'
import ContactForm from './ContactForm.js'
import Ablock from './Ablock.js'
import Title from './Title.js'

export const Maincontainer = () => {
    const items = [
        {
            'content': 'home',
            'location': 'Vancouer',
            'time': 'now',
            'what': `Creative web developer, love to learn and explore.`,
            'more' : false
        },
        {
            'content': 'education',
            'location': 'Simon Fraser University',
            'time': '2017 - 2021',
            'what': `Bachelor of Applied Science, Engineering Science`,
            'more' : false
        },
        {
            'content': 'intership',
            'location': 'Beijing',
            'time': 'May - Dec 2019',
            'what': `PwC China as Full Stack Intern`,
            'more' : true
        },
        {
            'content': 'intership',
            'location': 'West Vancouver',
            'time': '2018',
            'what': `Sager Education as Robotics Instructor`,
            'more' : true
        }
    ]

    return (
        <div className="main-container">
            {items.map((item) => (
                <Title item={item} />
            ))}
            <ContactForm />
            <Ablock />
        </div>
    )
}

export default Maincontainer
